import React from 'react';
import { useHistory } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { selectGuest, selectTotalMoney, selectItems } from '../../redux/cart/selectors';
import { emptyCart } from '../../redux/cart/actions';
import { formatVnd } from '../../utils/helpers';
import routes from '../../utils/routes';

import CartItem from '../atoms/cart-item.comp';
import {
  PageContainer,
  InfoHeading,
  SuccessCheckout,
  Red,
  Blue,
  Hr,
  InfoList,
  NormalLink,
  LinkToHome,
} from './checkout.styled';

function Checkout() {
  const history = useHistory();
  const dispatch = useDispatch();
  const guest = useSelector(selectGuest);
  const cartItems = useSelector(selectItems);
  const totalMoney = useSelector(selectTotalMoney);

  if (!cartItems.length) {
    history.push(routes.home.path);
  }

  return (
    <PageContainer>
      <SuccessCheckout>
        <ion-icon name="checkmark"></ion-icon> t hng thnh cng
      </SuccessCheckout>

      <p>
        Cm n{' '}
        <b>
          {guest.gender} {guest.name.split(/\s/).pop()}{' '}
        </b>{' '}
         cho Phonee c hi c phc v. Trong 10 pht, nhn vin Phonee s{' '}
        <b>gi tin nhn hoc gi in</b> xc nhn giao hng cho {guest.gender}.
      </p>

      <InfoHeading>Thng tin t hng:</InfoHeading>
      <InfoList>
        <li>
          {' '}
          Ngi nhn:{' '}
          <b>
            {' '}
            {guest.name}, {guest.phone}{' '}
          </b>{' '}
        </li>
        <li>
          a ch nhn hng:{' '}
          <b>
            {guest.street}, {guest.ward?.label}, {guest.district?.label}, {guest.province?.label}
          </b>
        </li>
        <li>
          Tng tin:{' '}
          <b>
            <Red> {formatVnd(totalMoney)} </Red>
          </b>
        </li>
      </InfoList>

      <NormalLink
        onClick={() => {
          if (window.confirm('Bn c chc mun hy n hng')) {
            dispatch(emptyCart());
            history.push(routes.home.path);
          }
        }}
      >
        Hy n hng
      </NormalLink>
      <p>
        Khi cn h tr vui lng gi{' '}
        <b>
          <Blue>1900.0000</Blue>
        </b>
      </p>

      <InfoHeading>Sn phm  mua:</InfoHeading>
      {cartItems.map(({ product, amount }) => (
        <CartItem key={product.id} product={product} amount={amount} fixed />
      ))}

      <Hr />

      <LinkToHome to={routes.home.path}>V trang ch</LinkToHome>
    </PageContainer>
  );
}

export default Checkout;
