export const priceFilters = [
  {
    label: 'Tt c',
    callback: () => true,
  },
  {
    label: 'Di 2 triu',
    callback: ({ price }) => price > 0 && price < 2000000,
  },
  {
    label: 'T 2 - 4 triu',
    callback: ({ price }) => price >= 2000000 && price <= 4000000,
  },
  {
    label: 'T 4 - 7 triu',
    callback: ({ price }) => price >= 4000000 && price <= 7000000,
  },
  {
    label: 'T 7 - 13 triu',
    callback: ({ price }) => price >= 7000000 && price <= 13000000,
  },
  {
    label: 'Trn 13 triu',
    callback: ({ price }) => price > 13000000,
  },
];

export const sorting = [
  {
    label: 'Ni bt',
    value: null,
  },
  {
    label: 'Gi: Tng dn',
    value: (a, b) => {
      if (!a.price) {
        // hang sap ve (price == 0)
        return 1;
      } else if (!b.price) {
        return -1;
      } else {
        return a.price - b.price;
      }
    },
  },
  {
    label: 'Gi: Gim dn',
    value: (a, b) => b.price - a.price,
  },
];
