import React from 'react';
import routes from '../../utils/routes';
import { formatVnd } from '../../utils/helpers';
import { useSelector } from 'react-redux';
import { selectItems, selectTotalMoney } from '../../redux/cart/selectors';

import CartItem from '../atoms/cart-item.comp';
import FormGuestCart from '../atoms/form-guest-cart.comp';
import {
  PageContainer,
  PageHeader,
  HeaderLink,
  CartContainer,
  CartProducts,
  Total,
  PayButton,
  MarginAuto,
  EmptyCartContainer,
  ToHomeLink,
} from './cart.styled';

function Cart() {
  const cartItems = useSelector(selectItems);
  const totalMoney = useSelector(selectTotalMoney);

  return (
    <PageContainer>
      <PageHeader>
        <HeaderLink to={routes.product.path}>
          <ion-icon name="chevron-back-outline" />
          Mua thm sn phm khc
        </HeaderLink>
        <p>Gi hng ca bn</p>
      </PageHeader>

      {cartItems.length > 0 ? (
        <CartContainer>
          <CartProducts>
            {cartItems.map(({ product, amount }) => (
              <CartItem key={product.id} product={product} amount={amount} />
            ))}

            <Total>
              Tng tin: <span>{formatVnd(totalMoney)}</span>
            </Total>
          </CartProducts>

          <FormGuestCart />

          <PayButton to={routes.checkout.path}>t hng</PayButton>
          <MarginAuto>Bn c th chn hnh thc thanh ton sau khi t hng</MarginAuto>
        </CartContainer>
      ) : (
        <EmptyCartContainer>
          Khng c sn phm no trong gi hng
          <ToHomeLink to={routes.home.path}>V trang ch</ToHomeLink>
        </EmptyCartContainer>
      )}
    </PageContainer>
  );
}

export default Cart;
